.host {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
}

.dashboardPanel {
  display: flex;
  flex-direction: column;
  border-radius: 7px;
  padding: 10px 10px 100px 10px;
}

.headerContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-bottom: 30px
}

.title {
  font-family: var(--app-text-font-mobile);
  font-size: 1.5rem;
  line-height: 1.5rem;
  color: var(--app-color-gray-dark);
}

.modalTitle {
  font-family: var(--app-text-font-mobile);
  font-size: 2rem;
  color: var(--app-color-gray-dark);
  margin-top: 5px;
  margin-bottom: 5px;
  text-align: center
}

.singleButton {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  outline: none;
  cursor: pointer;
  font-family: var(--app-text-main-font);
  font-weight: bold;
  font-size: 16px;
  padding: 10px;
  margin: 10px 10px 10px 0px;
  height: 40px;
  overflow: hidden;
  border: 1px solid var(--app-color-gray-lighter);
  background-color: var(--app-color-white-lightest);
  color: var(--app-color-gray-dark);
  stroke: var(--app-color-gray-dark);
  width: 220px;
}

.fieldRowContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: auto;
  margin-bottom: 10px;
  height: 32px;
  flex: 1 1;
}

.fieldTypeInput {
  ::placeholder {
    font-family: var(--app-text-font-mobile);
    font-size: 1rem;
    color: var(--app-color-red-error);
  }

  outline: none;
  border: none;
  padding-left: 10px;
  border: 0px;
  min-width: 350px;
  width: 100%;
  height: 37px;
  font-family: var(--app-text-font-mobile);
  font-size: 1rem;
  color: var(--app-color-gray-dark);
}

.dashboardCard {
  min-height: 50px;
  height: 140px;
  background-color: var(--app-color-white-lightest);
  cursor: pointer;
  width: 20%;
  min-width: 200px;
  margin: 0px 15px 0px 15px;
  border-radius: 8px;
  border: 1px dashed var(--app-color-gray-light);
  padding: 10px 20px 20px 20px;
  font-weight: bold;
  font-size: 16px;
  text-align: center;
  font-family: var(--app-text-font-mobile);

  p {
    text-align: center;
    font-size: 16px;
    color: var(--app-color-gray-dark);
    min-height: 40px;
  }
}


.newTaskButton {
  //background-color: #EC2027;
}

.searchContainer {
  width: 90%;
  display: flex;
  margin: 20px 0 0 0;
  border-bottom: 1px dashed var(--app-color-gray-light);

  & :hover {
    //border-bottom: 1px dashed var(--app-color-gray-light);
  }


}

.searchInput {
  width: inherit;
  border: 0;
  padding: 0 0 0 10px;
  font-size: 20px;
  font-family: var(--app-text-font-mobile);
  &::placeholder{
    font-size: 16px;
  }
}

.dropdownSelectComponent {
  border: none;
  width: 20%;
  margin-right: 20px;
}

.dropdownSelectMenuOverlayList {
  right: 0px;
  left: -30px;
  width: calc(100% + 30px);
  top: 76%;
  border-radius: 8px;
  //box-shadow: 0px 0px 30px #0000001a;
}

.dropdownSelectMenuOverlayListItem {
  color: var(--app-color-gray-dark);
  padding: 5px 0;
}

.dropdownInput {
  color: var(--app-color-gray-dark);
  font-size: 20px;
}

.dropdownClearControlsClassName {
  padding: 0 10px;
  color: var(--app-color-gray-darker);
}

.dropdownListContainer {
  border: 1px solid var(--app-color-gray-lighter);
  border-radius: 4px;
  min-height: 200px;
  width: 100%;
  padding: 12px;

  &[app-variant="error"] {
    border: 1px solid var(--app-color-red-error);
  }
}

.dropDownButtonContainer {
  width: 100%;
  border: 1px dashed var(--app-color-gray-light);
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  border-radius: 4px;
  padding: 12px;
}

.searchResults {
  margin-top: 50px;
  min-height: 160px;
  height: auto;
  min-width: 100%;
  max-width: 100%;
  border: 0;
  font-family: var(--app-text-font-mobile);
  font-size: 20px;
  background-color: white;
}

.pageContainer {
  display: flex;
  align-items: center;
  max-height: 80%;
  margin: 0;

  width: 10px p {
    height: 20px;
    font-family: var(--app-text-font-mobile);
    font-size: 16px;
    font-weight: 700;
    letter-spacing: 0;
    line-height: 20px;
  }
}

.emulatedFlexGap>* {
  //margin: 12px 0 0 12px;
  margin-left: 10px;
}

.emulatedFlexGap {
  padding-top: 10px;
  padding-bottom: 15px;
  display: inline-flex;
  flex-wrap: wrap;
  align-content: center;

  align-self: center;
}

.center {
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: center;
  align-content: center;
}

.logoUrlImg {
  object-fit: fill;
  height: 80px;
  width: 200px;
  margin: 10px;
}

.logoUrlImgMobile {
  object-fit: fill;
  height: 80px;
  width: 220px;
  margin: 5px;
}

.dashboardImg {
  object-fit: fill;
  height: 400px;
  width: inherit
}

.menuBar {
  display: flex;
  align-items: center;
  background-color: var(--app-color-gray-dark);
  font-family: var(--app-text-font-mobile);
  font-weight: 500;
  width: 100%;
  flex-direction: row;
  padding-left: 10px;
  margin-bottom: 40px;
  height: 180px;
}